import React,{useState, useEffect} from 'react';
import classes from './Nav.module.scss';
import logo from '../../images/logo.svg';
import {Link} from 'react-router-dom';

const Nav = (props) => {
    const [clicked, setClicked] = useState(false);
    const toggleHandler= () => {
        setClicked(!clicked);
    }

    return (  
        <nav>
            <ul className={clicked? `${classes.show} ${classes.left}`: classes.left}>
                <li><Link id="logo" to="/Women" onClick={toggleHandler}>WOMEN</Link></li>
                <li><Link id="logo" to="/Men" onClick={toggleHandler}>MEN</Link></li>
            </ul>
            <div className={classes.burger} onClick={toggleHandler}>
                <div className={classes.navline}></div>
                <div className={classes.navline}></div>
                <div className={classes.navline}></div>
            </div>
            <Link id="logo" to="/">
            <div className={classes.logo}>
                <img src={logo} alt="Logo"/>
            </div>
            </Link>
            <ul className={clicked? `${classes.show} ${classes.right}`: classes.right}>
                <li><Link id="logo" to="/about" onClick={toggleHandler}>ABOUT</Link></li>
                <li><Link id="logo" to="/contact" onClick={toggleHandler}>CONTACT</Link></li>
            </ul>
        </nav>
    );
}
 
export default Nav;